<template>
  <div class="listmenu">
    <!-- <el-button type="primary" @click="openFullScreen2"> 服务方式 </el-button> -->
    <el-table
      :data="adminGetter"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
      :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
    >
      <el-table-column prop="id" label="用户编号" sortable width="150">
      </el-table-column>
      <el-table-column
        prop="username"
        label="用户名"
        sortable
        width="300"
      ></el-table-column>

      <el-table-column
        prop="rolename"
        label="所属角色"
        sortable
        width="300"
      ></el-table-column>

      <el-table-column prop="address" label="操作">
        <template slot-scope="scope">
          <div>
            <!-- 这里自定义 表格 在template中 添加 slot-scope = scope -->
            <!-- 判断状态 如果说 后台返回的 status 是 1 就是启用 否则就禁用  -->
            <el-button type="primary" v-if="scope.row.status == 1" plain
              >启用</el-button
            >

            <el-button type="info" v-else plain>禁用</el-button>

            <el-button type="success" @click="edit(scope.row.uid)" plain
              >编辑</el-button
            >

            <delbutton @delbtn="del(scope.row.uid)"></delbutton>
          </div>
        </template>
      </el-table-column>
    </el-table>

    <!-- 进行分页处理  -->
    <!-- 其中 adminCount的值就是 总共页数 -->
    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="adminPage"
      :total="adminCount"
      @current-change="change"
    >
    </el-pagination>
  </div>
</template>
<script>
// 引入仓库
import { mapGetters, mapActions } from "vuex";
import { reqUserDel } from "../../../util/request";
import { successAlert, warningAlert } from "../../../util/alert";

export default {
  computed: {
    ...mapGetters({
      // 菜单请求
      adminGetter: "admin/adminGetter",
      // 获取总数
      adminCount: "admin/adminCount",
      adminPage: "admin/adminPage",
      adminnewpage: "admin/adminnewpage",
    }),
  },

  methods: {
    ...mapActions({
      actionAdminList: "admin/actionAdminList",
      actionAdminCount: "admin/actionAdminCount",
      actionAdminPage: "admin/actionAdminPage",
    }),

    // 删除函数
    del(uid) {

      reqUserDel(uid).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.actionAdminList();
          // 删除之后进行判断 页数是不是也要更改
          this.actionAdminCount();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
    // 编辑函数
    edit(id) {
      // 给父组建进行传参数
      this.$emit("listedit", id);
    },

    change(page) {
      console.log(page);
      this.actionAdminPage(page);
      // 重新发送请求
      this.actionAdminList();
    },
  },

  data() {
    return {
      flag: true,
    };
  },

  // 挂载完成之后 进行 调用 发送请求函数
  mounted() {
    this.actionAdminList();
    // 调用 接口获取总条数
    this.actionAdminCount();
  },
};
</script>

<style lang="">
.cell {
  text-align: center;
}

.el-table,
.submit {
  width: 100%;
  margin-top: 100px;
}
.submit {
  font-size: 16px;
}
</style>


