<template>
  <div>
    <el-dialog
      :title="isShow.edit ? '添加菜单' : '修改菜单'"
      :visible.sync="isShow.flag"
      @close="cancel"
    >
      <el-form ref="form" label-width="100px">
        <el-form-item label="所属角色:">
          <el-select v-model="form.roleid" placeholder="请选择">
            <!-- 循环便利 一下 下啦选项  -->
            <el-option label="请选择" :value="0"></el-option>

            <el-option
              v-for="item in gettersrole"
              :key="item.id"
              :label="item.rolename"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="用户名:" prop="name">
          <el-input v-model="form.username"></el-input>
        </el-form-item>

        <el-form-item label="密码:" prop="name">
          <el-input v-model="form.password"></el-input>
          <span class="p">留空不变</span>
        </el-form-item>

        <el-form-item label="状态" prop="delivery">
          <!-- active-value="1" :inactive-value = "2" -->
          <!-- 设置 什么值打开 什么值关闭  -->
          <el-switch
            v-model="form.status"
            :active-value="1"
            :inactive-value="2"
          ></el-switch>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" v-if="isShow.edit" @click="add()"
          >添 加</el-button
        >
        <!-- 修改按钮 通过 v-if控制  -->
        <el-button type="primary" @click="edit()" v-else>修 改</el-button>
      </div>
    </el-dialog>

  </div>
</template>


<script>
import { mapGetters, mapActions } from "vuex";
import { successAlert, warningAlert } from "../../../util/alert";
import { reqUserAdd, reqUserInfo, reqUserEdit } from "../../../util/request";

export default {
  props: ["isShow"],

  computed: {
    ...mapGetters({
      gettersrole: "role/gettersrole",
      adminGetter: "admin/adminGetter",
      adminCount: "admin/adminCount",
    }),
  },

  methods: {
    ...mapActions({
      actionrole: "role/actionrole",
      actionAdminList: "admin/actionAdminList",
      actionAdminCount: "admin/actionAdminCount",
    }),

    // 取消函数
    cancel() {
      this.form = {
        roleid: 0,
        username: "",
        password: "",
        status: 1,
      };
      this.isShow.flag = false;
    },

    // 进行添加函数 发送请求 然后 判断状态码
    add() {
      reqUserAdd(this.form).then((res) => {
        if (res.data.code == 200) {
          // 状态码正确进行 弹窗 清空 以及重新加载
          this.cancel();
          this.actionAdminList();
          // 用来控制 页数的显示
          this.actionAdminCount()

          successAlert(res.data.msg);
        } else {
          warningAlert(res.data.msg);
          this.cancel();
        }
      });
    },

    // 进行 获取某一条数据并添加进去
    getOne(id) {
      reqUserInfo(id).then((res) => {
        this.form = res.data.list;
        this.pas = this.form.password;
        this.form.password = "";
        this.form.uid = id;
      });
    },

    // 进行修改
    edit() {
      if ((this.form.password = "")) {
        this.form.password = this.pas;
      }

      reqUserEdit(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.actionAdminList();
          this.cancel();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },

  data() {
    return {
      form: {
        roleid: 0,
        username: "",
        password: "",
        status: 1,
      },
      // 这个用来记录一下 当前的密码 如果刘空就进行赋值 保证 原密码不便
      pas: "",
    };
  },

  mounted() {
    this.actionrole();
  },
};
</script>

<style>
</style>




